function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        loop: true,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        }
    })
}

function aboutSlider() {
    var swiper = new Swiper('.about .swiper-container', {
        slidesPerView: 1,
        navigation: {
            nextEl: '.about .swiper-button-next',
            prevEl: '.about .swiper-button-prev',
        },
        pagination: {
            el: '.about .swiper-pagination',
            type: 'bullets',
            clickable: true,
        }
    })
}

function coSlider() {
    var swiper = new Swiper('.co .swiper-container', {
        slidesPerView: 5,
        navigation: {
            nextEl: '.co .swiper-button-next',
            prevEl: '.co .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            576: {
                slidesPerView: 2,
            },
            768: {
                slidesPerView: 3,
            },
            992: {
                slidesPerView: 4,
            },
            1150: {
                slidesPerView: 5,
            },
        }
    })
}


$(document).ready(function() {
    $('input[type="tel"]').mask('+7 999 999-9999', { autoclear: false }, { placeholder: '+7             ' });
    mainSlider()
    aboutSlider()
    coSlider()

    $(".header__burger").click(function () {
        $(".header").toggleClass("_open-menu")
        $("body").toggleClass("fixed-body")
    })



});
